html{
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  background: #f6f6f6;
  user-select: none;
  font-family: "Montserrat", sans-serif;
  color: #777;
  initial-scale:1;
  transition: 0.7s ease;

  background-image: url("assets/bg.png");
}



body {

  margin: 0;
  font-family: "Montserrat", BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  opacity: 1;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}

br{
  margin-top: 0%;
}


App{
  width: 100%;
  overflow: hidden;
  height: 100%;
}


/* width */
::-webkit-scrollbar {
  width: 10px;
  margin-right: 10px;
}


/* Handle */
::-webkit-scrollbar-thumb:hover {
  background: rgba(237, 89, 168, 0.79);
}

::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background: rgba(147, 148, 149, 0.58);

}



/* layout styles */
nav {
  background:  var(--primary) ;
  background-blend-mode: screen;
  width: calc(100% - 34px);
  height: 60px;
  margin: 17px;
  align-items: center;
  display: flex;
  justify-items: center;
  flex: 1;
  vertical-align: center;
  align-content: center;
  padding-bottom: 3px;
  transition: 0.4s ease;
  background: rgba(246, 246, 246, 0.41);
  backdrop-filter: blur(1.2pt);
  position: fixed;
  z-index: 100;
  top: 0; /* required */
  border: #e82645 1px solid;
  border-width: 0px 0px 0px 0px;
  box-shadow: 2px 2px 12px 6px rgba(149, 146, 147, 0.16);
  border-radius: 50px;
}

nav:hover {
  background: #fff;
  box-shadow: 2px 2px 12px 10px rgba(232, 116, 155, 0.16);
}

nav a{
  vertical-align: center;
  color: var(--title);
}
nav .sidenav-trigger{
  margin: 0;
}

::placeholder{
  color: #8a8a8a;
}

::-ms-input-placeholder{
  color: #ccc;

}

FlatList.css {
  max-height: 400px;
}

input{
  font-family: "Montserrat", sans-serif;
  width: 80%;
  background: transparent;
  mask-border-mode: unset;
  font-size: 14px;
  box-shadow: none !important;
  border: none;
  padding: 20px;
  transition: 0.4s ease;
  border-bottom: 1px solid #949494;
  -webkit-box-shadow: none !important;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:window-inactive,
input:-webkit-autofill:focus,
input:-webkit-autofill:not(:focus),
input:-webkit-autofill:active{
  -webkit-background-clip: text;
  -webkit-text-fill-color: #aaa;
  -webkit-text-stroke-color: #aaa;
}

input:not(:focus){
  background-color: transparent;
  background: transparent;
}

button {
  background-color: transparent;
  font-family: "Montserrat" , sans-serif;
  font-size: 16px;
  padding-top:10px;
  padding-bottom: 10px;
  border-radius: 7px;
  padding-left: 10px;
  padding-right: 10px;
  transition: 0.4s ease;
  border: none;
  color: #fff;
}



:root{
  --primary: #f0f0f0;
  --secondary: rgba(11, 17, 45, 0.7);
  --title: #777;
  --third : #0F3042
}

input:focus{
  outline: none;
  border-bottom: 1px solid #b94949;
  box-shadow: none !important;
}
input:active{outline: none;
  border-bottom: 1px solid #b94949;
  box-shadow: none !important;
}

input.noner{
  border-bottom: 1px solid rgba(49, 49, 49, 0.079);
}

select{
  background-color: transparent; /* Set the background color to transparent */
  color: inherit; /* Inherit the text color from the input */
  background: transparent;
  transition: 0.4s ease;
  border-bottom: 1px solid #eee;
}

span {
  color: #777;
}

textarea{
  background:transparent;
