select {
  border: none;
  transition: 0.4s ease;
  border-bottom: 1px solid #dddddd;
  width: 88%;
  padding: 20px;
  font-family: "Montserrat", BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
}

select:focus{
  border:none;
  border-bottom: 1px solid #dddddd;
  outline: none
}

select:active{
  border:none;
  border-bottom: 1px solid #dddddd;
  outline: none
}

span:hover {
  background: transparent;
}

select option:focus{
  border-bottom: 1px solid #dddddd;
  background: #ff7676;
  color: #eee;
}


select option:hover{
  border-bottom: 1px solid #dddddd;
  background: #ff7676;
  color: #eee;
}


select option:active{
  border-bottom: 1px solid #dddddd;
  background: #ff7676;
  color: #eee;
}




select:-ms-keyboard-active {
  border: none;
}

select:-moz-window-inactive{
  border: none;
}

select::selection{
  border: none;
}

selector{
  border: none;
}

/* Handle */
.full-bg-img-mask::-webkit-scrollbar-thumb:hover {
  background: rgba(202, 202, 204, 0.79);
}

.full-bg-img-mask::-webkit-scrollbar-thumb {
  border-radius: 3px;
  background: rgba(11, 15, 23, 0.58);

}
.full-bg-img {
  /*background-image: url("assets/code.jpg");*/
  background-repeat: no-repeat;
  filter: blur(2.9px);
  opacity: 0.5;
  background-size: cover;
  position: absolute;
  width:  100%;
  height:  100%;
}

.full-bg-img-book {
  background-repeat: no-repeat;
  opacity: 0.9;
  background-size: cover;
  position: absolute;
  width:  100%;
  height:  100%;
}



@media screen  and (max-width: 792px) {
  .wrap-text-white{
    transition: 0.4s ease;
    width: 80%;
  }
}

.lengify{
  width: 650px;
}



@media screen and (max-width: 700px){
  .lengify{
    width: 330px;
  }
}

@media  screen and (max-width: 500px) {


  .mobile-nav-item4{
    visibility: visible;
  }
  .material-symbols-outlined{
    visibility: visible;
  }

  .small{
    transition: 0.5s ease;
    font-size: 11px;
  }

  p{
    font-size: 12px;
  }
}

