Light theme, dark front
 */

/*
Viewports
 */

.mobile {
  width: 0%;
  height: 0%;
  visibility: hidden;
  opacity: 0;
  transition: 0.4s ease;
}

.pc{
  width: 100%;
  height: 100%;
  visibility: visible;
  opacity: 1;
  transition: 0.4s ease;
}

@media screen and (max-width: 600px) {


  .mobile {
    visibility: visible;
    opacity: 1;
    width: 100%;
    height: 100%;
    transition: 0.4s ease;
  }

  .pc{
    width: 100%;
    visibility: hidden;
    opacity: 0;
    width: 0%;
    height: 0%;
    transition: 0.4s ease;
  }

}